.ourPolicyDetails {
  padding: 50px 20px;
  &__title {
    margin-bottom: 50px;
    text-align: center;
    font-size: 30px;
    font-weight: 600;
  }
  &__description {
    font-size: 15px;
    font-weight: 600;
  }
  &__additionalInfo {
    font-size: 12px;
  }
  &__ourCustomerService {
    margin-top: 30px;
    margin-bottom: 20px;
    font-size: 15px;
    font-weight: 700;
  }
  &__link {
    margin-right: 10px;
    margin-bottom: 5px;
    color: black;
    > span {
      font-size: 13px;
      font-weight: 700;
      margin-left: 5px;
    }
    & a {
      color: black;
      text-shadow: 1px 1px 2px black;
    }
  }
}

@media screen and (min-width: 760px) {
  .ourPolicyDetails {
    &__title {
      font-size: 35px;
    }
    &__description {
      font-size: 20px;
      font-weight: 500;
    }
    &__ourCustomerService {
      margin-top: 50px;
      font-size: 25px;
    }
    &__link {
      margin-right: 20px;
      > span {
        margin-left: 10px;
      }
    }
  }
}
