@import '../../_variables.scss';

.menu {
  width: 40%;
  &__icon {
    width: 40px;
    height: 40px;
    cursor: pointer;
    margin-right: 20px;
  }
}

.dropdownMenu {
  display: flex;
  position: fixed;
  top: 0;
  height: 100vh;
  z-index: 1500;
  animation: dropdownMenu-show 0.8s ease-in-out;
  &__body {
    background-color: white;
    width: 70vw;
    box-shadow: 10px 10px 5px 12px white;
    overflow: scroll;
    padding-bottom: 30px;
  }
  &__link {
    display: flex;
    align-items: center;
    padding: 20px 30px;
    border-bottom: 2px solid gray;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: $loadMoreColor;
  }
  &__link:hover {
    background-color: #cacaca;
  }
  &__icon {
    width: 30px;
    height: 30px;
    // border-radius: 50%;
    margin-left: 10px;
  }
  &__closeButton > img {
    cursor: pointer;
    width: 40px;
    height: 40px;
  }
  &__closeButton > img:hover {
    opacity: 0.7;
  }

  &__connectWithUs {
    &__title {
      display: flex;
      align-items: center;
    }
    &__links {
      display: flex;
      align-items: center;
      margin-top: 10px;

      > a > img {
        margin: 5px;
        width: 35px;
        height: 35px;
      }
    }
  }
}

@keyframes dropdownMenu-show {
  from {
    opacity: 0;
    transform: translateX(100);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media screen and (min-width: 450px) {
  .dropdownMenu {
    &__body {
      width: 60vw;
    }
  }
}

@media screen and (min-width: 650px) {
  .dropdownMenu {
    &__body {
      width: 35vw;
    }
  }
}

@media screen and (min-width: 950px) {
  .menu {
    &__icon {
      width: 50px;
      height: 50px;
    }
  }
  .dropdownMenu {
    &__body {
      width: 25vw;
    }
  }
}
