.footer {
  padding: 50px 20px 100px 20px;
  background-color: #383838;
  & a {
    text-decoration: none;
  }
  &__main_title {
    color: rgb(255, 255, 255);
    font-size: 16px;
    margin-bottom: 30px;
  }
  &__discretion {
    color: #878787;
    margin-bottom: 25px;
  }

  &__phone {
    color: white;
    margin-bottom: 25px;
    > img {
      width: 24px;
      height: 24px;
      margin-left: 5px;
      vertical-align: middle;
    }
  }

  &__followUsOn {
    color: white;
    margin-bottom: 25px;
    > a > img {
      margin: 5px;
      width: 30px;
      height: 30px;
    }
  }

  &__whatsappLink {
    position: fixed;
    bottom: 10px;
    right: 16px;
    z-index: 1000;
    cursor: pointer;
    color: black;
    > div {
      font-size: 12px;
      border: 1px solid gray;
      border-radius: 5px;
      padding: 2px;
      box-shadow: 1px 1px 1px 1px gray;
      margin-bottom: 5px;
      font-weight: 700;
      background-color: white;
    }
    > img {
      width: 50px;
      height: 50px;
    }
  }
}

@media screen and (min-width: 450px) {
  .footer {
    padding: 50px 70px 100px 70px;
    display: flex;
    &__col {
      width: 50%;
      padding: 0 20px;
    }
    &__whatsappLink {
      > div {
        font-size: 16px;
      }
    }
  }
}
