@import '../../_variables.scss';

.shopByBrandSlider {
  &__buttons {
    margin: 50px 15px 15px 15px;
    background-color: #f6f6f6;
    height: 40px;
    .react-slideshow-container {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  &__button {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    padding: 0 2px;
    height: 35px;
    cursor: pointer;
  }
  &__button:hover,
  .selectedBrand {
    color: $hoverColor;
    border: 2px solid $hoverColor;
    border-radius: 20px;
  }
  &__products {
    max-height: 500px;
  }
}

.shopByBrandSlider__products .react-slideshow-container .nav {
  top: 65px;
  width: 40px;
  height: 40px;
}

@media screen and (min-width: 760px) {
  .shopByBrandSlider__products .react-slideshow-container .nav {
    top: 110px;
    width: 50px;
    height: 50px;
  }
}
